import React from "react"
import imageURLs from "../../data/content-cdn";
import Img from "gatsby-image";
const isMobile = typeof window !== 'undefined' && window.innerWidth  < 940;
const bgImage = isMobile ? "Mobile" : "Desktop"


const HomeImage = ({ data }) => {
  console.log(data);
  return (
  <Img
      fluid={isMobile 
        ? data.homeMobile.childImageSharp.fluid
        : data.homeDesktop.childImageSharp.fluid
      }
      alt="Shai brides in fairground"
      className={`homeImage${bgImage}`}
  />
  )
}

export default HomeImage;

