import React from "react"
import imageURLs from "../data/content-cdn";
import HomeImage from "../components/home/HomeImage";
import Layout from "../components/layout"
import SEO from "../components/seo"
import Joie from "../components/joie"
import Tokyo from "../components/tokyo"
import LiveList from "../components/LiveList"
import Footer from "../components/footer";
import ScrollableAnchor, { configureAnchors } from 'react-scrollable-anchor';
const isMobile = typeof window !== 'undefined' && window.innerWidth  < 480;
const bgImage = isMobile ? "Mobile" : "Desktop";
configureAnchors({scrollDuration: 600})


const IndexPage = ({ data }) => (
  <Layout>
    <SEO title="SHAI BRIDES" />
    <div className="indexContainer">
      <HomeImage data={data} />
    </div>
    <div className={`logoHomeDiv${bgImage}`}>
            <img src={imageURLs.Logos.white} className={`logoHome${bgImage}`} />
        </div>
    <div className={`arrowHomeDiv${bgImage}`}>
      <a href='#live' >
            <img src={imageURLs.Icons["Down-arrow"]} className={`downIcon${bgImage}`} />
      </a>
    </div>
    <ScrollableAnchor id={'live'}>
      <div className="emptyDiv" />
    </ScrollableAnchor>
    <div className="is-mint-bg landingLiveDiv">
      <h1>UPCOMING SHOWS</h1>
      <LiveList data={data}/>
    </div>
    <ScrollableAnchor id={'joie'}>
      <div className="emptyDiv" />
    </ScrollableAnchor>
    <Joie />
    <ScrollableAnchor id={'tokyo'}>
      <div className="emptyDiv" />
    </ScrollableAnchor>
    <Tokyo />
    <Footer bgColour="dark-blue"/>
  </Layout>
)


export const query = graphql`
  query {
    homeDesktop: file(relativePath: { eq: "homeDesktop.jpg" }) {
    childImageSharp {
        fluid(maxWidth: 1240) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    homeMobile: file(relativePath: { eq: "homeMobile.jpg" }) {
      childImageSharp {
          fluid(maxWidth: 800) {
            ...GatsbyImageSharpFluid
          }
        }
      }
  }
 `

export default IndexPage;

